import axios from '@axios'

export default {
  namespaced: true,
  state: {},
  getters: {},
  mutations: {},
  actions: {
    fetchUsers(ctx, queryParams) {
      return new Promise((resolve, reject) => {
  
        let data = {
          params: {
            params: queryParams
          },
          headers: {
              "x-access-token": localStorage.getItem('accessToken'),
              "content-type": "application/json"
          }
      };
      
      
        axios
          .get('http://54.218.130.89:3000/api/admin/user/list', data)
          .then(response => resolve(response)) 
          .catch(error => reject(error))
      })
   
    },
    fetchUser(ctx, { id }) {
      return new Promise((resolve, reject) => {

        let data = {
        
          headers: {
              "x-access-token": localStorage.getItem('accessToken'),
              "content-type": "application/json"
          }
      };

        axios
          .get(`http://54.218.130.89:3000/api/admin/user/${id}`,data)
          .then(response => {console.log(response.data);resolve(response)})
          .catch(error => reject(error))
      })
    },
    addUser(ctx, userData) {
      return new Promise((resolve, reject) => {
        axios
          .post('/apps/user/users', { user: userData })
          .then(response => resolve(response))
          .catch(error => reject(error))
      })
    },
  },
}
